<template>
	
	<view class="content">
		
		<view class="mask" v-if="loading" style="background-color:#F5F5F5;height:100%!important;" :style="{'margin-top':h5Top+'upx'}">
			<view class="loading13" style="position:absolute;margin-top:-100upx;">
				<view></view>
				<view></view>
				<view></view>
				<view></view>
				<view></view>
			</view>
		</view>
		<view class="center-content">
			<view class="content-head">
				<image class="tx left" :src="info.headimgurl" mode="aspectFill" @click="goHome(info.uid)"></image>
				<view class="username">{{info.username}}</view>
				<view class="is-my right" v-if="info.isMe">我发布的</view>
			</view>
			<view class="content-center">
				<view class="text">{{info.text}}</view>
				<view class="imgs">
					<jyf-parser use-cache :html="imgList" :height="'100px'" ref="article" v-if="imgList.length>0"></jyf-parser>
				</view>
			</view>
			<view class="content-bottom">
				<text>{{utils.timeTodate('Y年m月d日 H:i',info.add_time+'')}}</text>
			</view>
		</view>
		
		<view class="ad-list">
			<view class="ad-info" v-for="(a, i) in adList" :key="i" @click="skipUrl(a.url, 'web')">
				<image :src="a.content" mode="aspectFill" :style="{'height':a.height+'px'}"></image>
			</view>
		</view>
		
		<view class="comment-bottom">
			<view class="comment-bottom-top">
				<text>全部评论({{commentCount}})</text>
			</view>
			<view class="comment-bottom-content">
				
				<view class="comment-bottom-info" v-for="(c, i) in commentList" :key="i">
					<view class="comment">
						<view class="top" @click="goHome(c.member.id)"> 
							<image class="left" :src="c.member.headimgurl" mode="aspectFill"></image>
							<text style="margin-left:15upx;">{{c.member.username}}</text>
							<text class="right date">{{c.add_time}}</text>
						</view>
						<view class="comment-info">{{c.content}}</view>
					</view>
				</view>

				<view class="send-comment">
					<input type="text" @input="setContent" :value="content" placeholder="我也来说几句" :maxlength="50" :cursor-spacing="10"/>
					<text class="btn right" @click="sendPostComment">评论</text>
				</view>
			</view>
			<view style="width:100%;text-align:center;margin:100upx 0;" v-if="commentCount==0">
				<image src="@/static/imgs/nodata.png" style="width:280upx;height:180upx;"></image>
				<view style="width:100%;font-size:25upx;color:#666;margin-top:30upx;">暂无数据</view>
			</view>
		</view>
	</view>
</template>
<script>
import api from "@/common/api.js";
import utils from '@/components/shoyu-date/utils.filter.js';
var _self;
export default {
	data() {
		return {
			utils: utils,
			loading: true,
			isLogin: false,
			userId: 0,
			phone: {},
			h5Top: 0,
			page: 1,
			info: {
				username: 'loading...',
				isMe: false,
			},
			imgList: "",
			adList: [],
			
			commentList: [],
			commentCount: 0,
			content: '',
			
			type: 1,
		}
	},
	onLoad(e) {
		_self = this;
		_self.type = e.t;
		_self.isLogin = api.getLogins();
		if(_self.isLogin) _self.userId = _self.isLogin.userId;
		_self.phone = uni.getSystemInfoSync();
		// #ifdef H5
		_self.h5Top = 90;
		// #endif
		//console.log(e)
		_self.initData();
	},
	methods: {
		setContent(e) {
			//console.log(e.detail.value)
			_self.content = e.detail.value;
		},
		sendPostComment() {
			//console.log(_self.content.length)
			if(!_self.isLogin){
				api.showToast('请登录后再发来帖');
				setTimeout(()=>{
					_self.skipUrl('/pages/login/login', 'new');
				}, 500);
				return;
			}
			if(_self.content.length < 1) {
				api.showToast('请输入评论内容', 1500);
				return;
			}
			uni.showLoading({ title:'发布中...',mask:true });
			uni.request({
				url: api.apiData.sendComment,
				method: 'POST',
				data: {
					post_id: _self.info.id,
					userId: _self.userId,
					content: _self.content,
					pid: 0
				},
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e)=> {
					var r = e.data;
					api.showToast(r.Msg, 2000);
					if(r.Code == 201) return
					_self.content = '';
					_self.getData();
				},
				fail: ()=> {
					// 加载失败提示
					api.showToast("服务器连接失败，请检查网络是否正常", 3000);
				},
				complete: ()=> {
					setTimeout(()=>{
						_self.loading = false;
					}, 500);
				}
			});
		},
		goHome(uid) {
			if(uid == 0){
				api.showToast('用户账号异常', 1500);
				return;
			}
			var param = {
				url: '/pages/plaza/info?t=1',
				t: 'new'
			}
			//console.log(uid)
			_self.skipUrl('/pages/member/home_page?type='+JSON.stringify(param)+'&uid='+uid, 'new');
		},
		initData() {
			uni.getStorage({
			    key: 'info_content_' + api.appkey,
			    success: (res)=> {
			       //console.log(res.data);
					_self.info = res.data;
					var imgList = _self.info.allImg;
					if(imgList.length > 0){
						for (var i = 0; i < imgList.length; i++) {
							//console.log(i)
							_self.imgList += '<img src="'+imgList[i]+'">';
						}
					}
					//console.log(_self.imgList)
			    },
				fail: ()=> {
					api.showToast('数据异常，请稍后重试', 1000);
					setTimeout(()=>{
						_self.skipUrl('/pages/plaza/index', 'new');
					}, 500)
				},
				complete: ()=> {
					_self.getAd();
					_self.getData();
				}
			});
		},
		getAd() {
			uni.request({
				url: api.apiData.getAdInfo,
				method: 'POST',
				data: {
					position_id: 8,
					limit: 1
				},
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e)=> {
					//console.log(e);
					var r = e.data;
					if(r.Code == 200) {
						_self.adList = r.Data;
					}
				}
			});
		},
		getData() {
			//if(_self.page > 1) uni.showLoading({ title:'正在载加...',mask:true });
			uni.request({ 
				url: api.apiData.getComment,
				method: 'POST',
				data: {
					post_id: _self.info.id
				},
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: (e) => {
					//console.log(e)
					var r = e.data;
					if(r.Code == 201){
						api.showToast(r.Msg, 2000);
						return
					} 
					_self.commentList = r.Data;
					//console.log(_self.commentList)
					_self.commentCount= _self.commentList.length;
				},
				fail: ()=> {
					// 加载失败提示
					api.showToast("服务器连接失败，请检查网络是否正常", 3000);
				},
				complete: ()=> {
					setTimeout(()=>{
						_self.loading = false;
					}, 500);
				}
			});
		},
		// 跳转登录
		skipUrl(uri = '/pages/login/login', type = 'new') {
			api.jumpUrl(uri, type)
		},
	},
	onPullDownRefresh() {
		_self.getAd();
		_self.getData();
		uni.stopPullDownRefresh();
	},
	onBackPress(e) {
		if(_self.type == 1){
			api.jumpUrl('/pages/plaza/index', 'tab');
			return true;
		}
	},
};
</script>

<style>
	@import url('@/components/loading/loading.css');
	.send-comment .btn{
		display: inline-block;
		color: #FFFFFF;
		font-size: 26upx;
		height: 30px;
		width: 20%;
		line-height: 30px;
		border-radius: 30px;
		text-align: center;
		margin-right: 5%;
		margin-top: 10px;
		background-image: linear-gradient(to right,rgba(255,12,208,0.8), rgba(254,67,101,0.8));
	}
	.send-comment input{
		height: 30px;
		width: 60%;
		border: 1px solid #F5F5F5;
		margin: 9px 0 0 5%;
		border-radius: 36px;
		padding: 0 20upx;
		font-size: 26upx;
		background-color: #F8F8F8;
		display: inline-block;
	}
	.send-comment{
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 50px;
		background-color: #FFFFFF;
	}
	.comment-bottom-info .comment-info{
		font-size: 26upx;
		margin: 15upx 0 10upx;
		color: #444;
		//background-color: #007AFF;
	}
	.comment-bottom-info .comment .top .date{
		font-size: 26upx;
		color: #999999;
	}
	.comment-bottom-info .comment .top{
		font-size: 28upx;
	}
	.comment-bottom-info .comment image{
		width: 45upx;
		height: 45upx;
		border-radius: 50%;
	}
	.comment-bottom-info {
		border-bottom: 1px solid #F5F5F5;
		padding-bottom: 10upx;
		margin-bottom: 20upx;
	}
	.comment-bottom-content{
		//height: 100upx;
		margin-top: 15upx;
		margin-bottom: 50px;
		//background-color: #007AFF;
	}
	.comment-bottom-top{
		font-size: 30upx;
	}
	.comment-bottom{
		margin-top: 12upx;
		padding: 15upx 22upx;
		background-color: #FFFFFF;
	}
	.ad-list image{
		width: 100%;
		border-radius: 10upx;
		margin-bottom: -10upx;
	}
	.ad-list{
		margin-top: 15upx;
		margin-bottom: 0;
		padding: 0 20upx;
		//background-color: #FFFFFF;
	}
	.content-bottom{
		margin: 15upx 0 0;
		font-size: 24upx;
		color: #999999;
		text-align: right;
	}
	.content-center .imgs{
		margin-top: 15upx;
	}
	.content-center .text{
		font-size: 28upx;
		color: #000000;
	}
	.content-center{
		margin-top: 15upx;
	}
	.content-head .is-my{
		display: inline-block;
		//background-image: linear-gradient(to right,rgba(255,12,208,0.8), rgba(254,67,101,0.8));
		color: rgba(255,12,208,0.8);
		font-size: 24upx;
		height: 60upx;
		line-height: 60upx;
		width: 120upx;
		border-radius: 60upx;
		text-align: center;
		border: 1px solid rgba(255,12,208,0.8);
		margin-top: 10upx;
	}
	.content-head .username{
		display: inline-block;
		height: 80upx;
		line-height: 80upx;
		width: 50%;
		margin-left: 15upx;
		font-size: 30upx;
		font-weight: bold;
	}
	.content-head .tx{
		width: 80upx;
		height: 80upx;
		border-radius: 50%;
	}
	.center-content{
		//height: 300upx;
		background-color: #FFFFFF;
		margin-top: 15upx;
		padding: 20upx 25upx;
	}
	.left{
		float: left;
	}
	.right{
		float: right;
	}
	page{
		background-color: #F5F5F5;
	}
</style>
